import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, ModalController, ToastController, ViewController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { BaseUI } from '../../common/baseui';
import { RestProvider } from '../../providers/rest/rest';
import { HeadfacePage } from '../headface/headface';

@Component({
  selector: 'page-user',
  templateUrl: 'user.html',
})
export class UserPage extends BaseUI {
  headface: string = "https://imoocqa.gugujiankong.com/users/5996953615f87ec629cff319.jpg?1519636874027";
  nickname: string = "加载中...";
  errorMessage: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public modalCtrl: ModalController,
    public loadCtrl: LoadingController,
    public rest: RestProvider,
    public storage: Storage,
    public toastCtrl: ToastController,
    public viewCtrl: ViewController,
  ) {
    super();
  }

  ionViewDidLoad() {
    this.loadUserPage();
  }

  /**
   * 加载用户数据
   * 
   * @memberof UserPage
   */
  loadUserPage() {
    this.storage.get('UserId').then((val) => {
      if (val != null) {
        //加载用户数据
        var loading = super.showLoading(this.loadCtrl, "加载中...");
        this.rest.getUserInfo(val)
          .subscribe(
          userinfo => {
            this.nickname = userinfo["UserNickName"];
            this.headface = userinfo["UserHeadface"] + "?" + (new Date()).valueOf();
            loading.dismiss();
          },
          error => this.errorMessage = <any>error
          );
      }
    });
  }

  /**
   * 保存用户更新的昵称
   * 
   * @memberof UserPage
   */
  updateNickName() {
    this.storage.get('UserId').then((val) => {
      if (val != null) {
        var loading = super.showLoading(this.loadCtrl, "修改中...");
        this.rest.updateNickName(val, this.nickname)
          .subscribe(
          f => {
            if (f["Status"] == "OK") {
              loading.dismiss();
              super.showToast(this.toastCtrl, "昵称修改成功。");
            } else {
              loading.dismiss();
              super.showToast(this.toastCtrl, f["StatusContent"]);
            }
          }
          )
      }
    })
  }

  /**
   * 注销按钮
   * 
   * @memberof UserPage
   */
  logout() {
    this.storage.remove('UserId');
    this.viewCtrl.dismiss();
  }

  /**
   * 跳转到更改头像页面
   * 
   * @memberof UserPage
   */
  gotoHeadface() {
    this.navCtrl.push(HeadfacePage);
  }
}



